:root {
  --main-color: #3d4e61;
  --border-color: #e1e7ed;
}

* {
  box-sizing: border-box;
}

html {
  color: var(--main-color);
  src: url("Avenir.8bcf8c72.ttc");
  font-family: Avenir, sans-serif;
  font-weight: 300;
}

body {
  src: url("Avenir.8bcf8c72.ttc");
  color: var(--main-color);
  margin: 0;
  padding: 0;
  font-family: Avenir, sans-serif;
  font-weight: 300;
}

@font-face {
  font-family: Avenir, sans-serif;
  src: url("Avenir.8bcf8c72.ttc");
  font-weight: 300;
  color: var(--main-color);
}

.form {
  margin: 20px 8px 0;
  padding: 0;
  position: relative;
}

.form__input {
  border: 1px solid var(--border-color);
  background: #fff;
  border-radius: 4px;
  width: 100%;
  display: block;
  box-shadow: 0 1px 7px #3d4e611a;
}

.form__input--big {
  background: url("Search.73266d21.svg") 22px 22px / 19px 19px no-repeat;
  height: 70px;
  padding: 26px 62px;
  box-shadow: 0 1px 8px #3d4e611a;
}

.form__input--small {
  background: url("Search.73266d21.svg") 12px 14px / 11px 11px no-repeat;
  height: 42px;
  padding: 11px 33px 12px;
  box-shadow: 0 1px 8px #3d4e611a;
}

.form__input::placeholder {
  color: inherit;
  margin: 0;
  padding: 0;
  font-family: inherit;
}

.form__input--big::placeholder {
  font-size: 16px;
}

.form__input--small::placeholder {
  font-size: 14px;
}

.form__input--big:hover, .form__input--small:hover {
  box-shadow: 0 4px 4px #00000040;
}

.form__input--big:focus, .form__input--small:focus {
  background: url("Search.73266d21.svg") linear-gradient(180deg, #fff 0%, #f6f6f7 100%);
  outline: none;
  font-weight: 900;
}

